/* src/app/app.css */
.container {
  display: flex;
  height: 100vh;
  min-height: 100vh;
}
.app-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 12px;
  background:
    linear-gradient(
      90deg,
      #ff6b6b,
      #a855f7 60%);
  color: white;
}
.app-header-title {
  font-weight: 600;
}
.left-section,
.right-section {
  flex: 1 1 50%;
  min-width: 0;
  display: flex;
  flex-direction: column;
}
